.SelectColorPanel{padding: 8px 0; }

.header{
    display:  flex;
    gap: 8px;
    padding: 8px 0;
    margin-bottom: 8px;
}
.header-radio-wrapper{
    flex-grow: 1;
   
    input{display:  none;}
    label{
         padding: 6px 12px;
        border-bottom: 2px solid transparent;
    }
    input:checked + label {
        color: var(--LimeGreen);
        border-bottom-color: var(--LimeGreen);
    }
}
.main{
    --width : 250px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    gap:3px;
    width: var(--width);
}
.item-wrapper{    
     width:calc(var(--width) / 10);
     height:calc(var(--width) / 10);
    input{
        display: none;
    }
    input:checked + label {
        border-color: var(--LimeGreen);
    }
    label:hover{
        border-color: var(--LimeGreen);
        transform: scale(1.1);
    }
    label{
        display: inline-block;
        transition: all 250ms linear;
        width: 100%;
        height: 100%;
        border: 2px solid transparent;
        border-radius: 3px;
        cursor: pointer;
    }
}