.float-form-content {
    border-top: 2px solid var(--border-color);
	width: 100%;
	form {
		display: flex;
		gap: 16px;
        .textarea{   
            user-select: none;
			-webkit-appearance: none;
			appearance: none;
			background-color: var(--background-pri);
			width: 100%;
			padding:8px 16px;
			color: inherit;
			font-size: 1.25rem;
			cursor: text;
            border: none;
            border-top: 1px solid var(--border-color);
			outline:none;
			&:focus {
				border-color: var(--LimeGreen);
			}
        }
		.btn-group {
			display: flex;
			flex-direction: column;
			gap: 8px;
			justify-content: flex-end;
			button {
				-webkit-appearance: none;
				appearance: none;
				background-color: transparent;
				padding:6px;
				border: 1px solid var(--border-color);
				border-radius: 3px;
				display: block;
				color: var(--text-color);
				font-size: 1rem;
				cursor: pointer;
                &:focus {
				border-color: var(--LimeGreen);
			    }
			}
			.reset-btn {
				&:hover {
					color: var(--BrightRed);
					border-color: var(--BrightRed);
				}
			}
			.add-btn {
				&:hover {
					color: var(--LimeGreen);
					border-color: var(--LimeGreen);
				}
			}
		}
	}
}


.wrapper {
	min-width: 50px;
	height: 100%;
	border: 2px solid var(--border-color);
	background-color: var(--background-sec);
	overflow: overlay;
	user-select: none;
}
.header {
	top: 0;
	left: 0;
	position: sticky;
	display: flex;
	justify-content: flex-end;
	min-height: 25px;
}
.close-edit-menu {
	transition: all 0.5s ease;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	font-size: 1rem;
	cursor: pointer;
	border: none;
	color: var(--BrightRed);
	&:hover {
		filter: brightness(1.25);
	}
}
.tippy-wrapper {
	padding: 16px ;
	background-color: var(--background-sec);
	border: 2px solid var(--border-color);
}

.props-input {
	max-width: 125px;
}
.text_input {
	user-select: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: var(--background-pri);
	width: 100%;
	padding: 15px;
	border: 2px solid var(--border-color);
	color: inherit;
	font-size: 1.25rem;
	cursor: text;
}

.section_heading {
	font-weight: 700;
	text-transform: capitalize;
	padding: 8px 0px;
}
.flex-col {
	display: flex;
	flex-direction: column;
	padding-left: 8px;
	gap: 8px;
}

.flex-row {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 15px;
	padding: 8px;
}
.btn-remove-element {
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	padding: 8px 12px;
	border: 1px solid var(--border-color);
	border-radius: 3px;
	cursor: pointer;
	transition: color 250ms ease;
	color: var(--text-color);
	&:hover {
		color: red;
	}
}

.group-btns {
	margin-top: 8px;
	display: flex;
	flex-direction: column;
	.tag-btn {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		padding: 8px 12px;
		border: 1px solid var(--border-color);
		border-radius: 3px;
		cursor: pointer;
		transition: color 250ms ease;
		color: var(--text-color);
		&:active {
			color: red;
		}
		&:hover {
			color: var(--LimeGreen);
		}
	}
}

.row {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	gap: 15px;
	& > input {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		padding: 3px;
		border: none;
		border: 2px solid var(--border-color);
		color: var(--text-color);
		font-size: 1.25rem;
		cursor: default;
	}
}
.selecedElement {
	outline: 1px dashed var(--BrightRed);
	outline-offset: 8px;
}

.select-wrapper {
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	border: 2px solid var(--border-color);
	border-radius: 3px;
	padding: 3px 6px;
	font-size: 1.25rem;
	color: inherit;
	max-width: 125px;
	width: 100%;
	cursor: pointer;
	.option {
	background-color: var(--background-sec)!important;
}
}

.div-center{
	padding: 8px 12px;
	text-align: center;
	&:hover{
		color: var(--LimeGreen);
	}
}
.buttons {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.checkbox-wrapper {
	border: 2px solid var(--border-color);
	border-radius: 3px;
	padding: 8px 12px;
	input {
		display: none;
	}
	input:checked + label {
		color: var(--LimeGreen);
	}
	label {
		cursor: pointer;
	}
	&:hover {
		color: var(--LimeGreen);
	}
}
