.wrapper{
    background-color: var(--background-sec);
    padding:8px 15px;
    display: flex;
    gap: 8px;
    border-radius: 6px;
    .tabs{
        flex-grow: 1;
        border-right: 1px solid var(--border-color);
        display: flex;
        gap: 8px;
        overflow-x: auto;
    }
}

.Enable_fn_btns{
    margin-left: auto;
    display: flex;
    align-items: center;
    gap: 16px;
    .btn-wrapper{
        border: 1px solid var(--border-color);
        border-radius: 3px;
        padding: 3px;
        input{display: none;}
        input:checked + label{
            color: var(--LimeGreen);
        }
       label{
        cursor: pointer;
       } 
    }
}

.tab_btn_wraper{
    border-right: 1px solid rgb(0 0 0 / 12%);
    border-bottom: 1px solid transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:4px 8px;
    &.tabActive{
        color: var(--LimeGreen);
    }
    &:not(:last-child){
          border-right: 1px solid var(--border-color);
    }
}
.tab_btn_wraper:hover .tab_btn_delete{
    visibility: visible;
}
.tab_btn {
     text-transform: capitalize;
	transition: color 0.5s ease;
	background-color: transparent;
	font-size: 1rem;
	min-height: 16px;
	cursor: pointer;
    border: none;
	color: inherit;
	&[tabindex]:focus {
		border-color: transparent;
		outline: 2px var(--LimeGreen) solid;
	}
}

.tab_btn_delete{
    margin-left: 8px;
    transition: all 0.5s ease; 
    display: inline-flex;
    visibility: hidden;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
	font-size: 1rem;
	cursor: pointer;
    border: none;
	color: var(--BrightRed);
    &:hover{
        filter: brightness(1.25);
    }
}
